import { ApiPropertyOptional } from '@nestjs/swagger';
import { IsEnum, IsOptional } from 'class-validator';
import { FolderType, SortType } from '../enums/folder-type.enum';
import { BaseQuery } from '@shared/interfaces/base-query.interface';

export class QueryFolderDto extends BaseQuery {
  @ApiPropertyOptional({ enum: FolderType })
  @IsOptional()
  @IsEnum(FolderType, { message: 'type must be either NOTE or DIARY' })
  type?: FolderType;

  @ApiPropertyOptional({ enum: SortType })
  @IsOptional()
  @IsEnum(SortType, { message: 'sort must be either ASC or DESC' })
  sort?: SortType;
}
